import React from "react";
import { Link, graphql } from "gatsby";

import Layout from "../../components/layout";
import SEO from "../../components/seo";
import { rhythm } from "../../utils/typography";
import './blog-post.scss';

const BlogPostTemplate = ({ data, pageContext, location }) => {
  const post = data.markdownRemark
  const siteTitle = data.site.siteMetadata.title
  let { previous, next } = pageContext;

  // Obtenemos el idioma del la ruta actual
  const actual_location = location.pathname.split('/').filter((el) => el !== '')[0];

  if(previous !== null) {
    previous = previous.filter((prev) => prev.node.fields.slug.startsWith(`/${actual_location}/`));
    const index_post = previous.findIndex((prev) => prev.node.fields.slug === location.pathname);
    next = (previous[index_post + 1] !== undefined) ? previous[index_post + 1] : null;
    previous = (previous[index_post - 1] !== undefined) ? previous[index_post - 1] : null;
  }

  return (
    <Layout location={location} title={siteTitle}>
      <SEO
        title={post.frontmatter.title}
        description={post.frontmatter.description || post.excerpt}
      />
      <article>
        <header className="header-post">
          <div className="container-md">
            <div className="row">
              <div className="col-12 mt-5 text-left pl-5">
                <h1 className="mb-0 pb-0">
                  {post.frontmatter.title}
                </h1>
                <div className="post-tags">
                  <span>Tags</span>
                </div>
                <p>
                  {post.frontmatter.date}
                </p>
              </div>
            </div>
          </div>
        </header>
        <div className="container-md pl-0 pr-0">
          <section className="post-body" dangerouslySetInnerHTML={{ __html: post.html }} />
        </div>
        <hr
          style={{
            marginBottom: rhythm(1),
          }}
        />
      </article>

      <nav className="container after-before--links">
        <ul className="row">
          <li className="col-6">
            {previous && (
              <Link to={previous.node.fields.slug} rel="prev">
                ← {previous.node.frontmatter.title}
              </Link>
            )}
          </li>
          <li className="col-6 text-right">
            {next && (
              <Link to={next.node.fields.slug} rel="next" className="text-white-color">
                {next.node.frontmatter.title} →
              </Link>
            )}
          </li>
        </ul>
      </nav>
    </Layout>
  )
}

export default BlogPostTemplate

export const pageQuery = graphql`
  query BlogPostBySlug($slug: String!) {
    site {
      siteMetadata {
        title
      }
    }
    markdownRemark(fields: { slug: { eq: $slug } }) {
      id
      excerpt(pruneLength: 160)
      html
      frontmatter {
        title
        date(formatString: "DD/MM/YYYY")
        description
        tags
      }
    }
  }
`
